.scrim {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(20, 20, 20, .6)));
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0), rgba(20, 20, 20, .6));
  background: -o-linear-gradient(top, rgba(0, 0, 0, 0), rgba(20, 20, 20, .6));
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(20, 20, 20, .6));
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  min-height: 100%;
}

.call_to_action_buttons {
  position: fixed;
  bottom: 50px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}


